* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  min-height: 100vh;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-container {
  min-height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

/*  */
.content-management-container {
  margin: 0;
  padding: 20px;
  width: 100%;
  min-height: 100vh;
  background-color: #f5f5f5;
}

/*  */
.login-page-wrapper {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

/*  navbar  */
.page-container {
  width: 100%;
  flex: 1;
  padding-top: 0; /*  navbar  navbar  */
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 60px); /*  navbar  */
}

/*  */
.ant-layout {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.ant-layout-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

footer {
  margin-top: auto;  /*  footer  */
} 